import DeepData from "../../assets/js/formate.js";

export default {
  name: "agent_config",
  data() {
    return {
      searchConfig: "",
      title: "新增",
      visible: false,
      total: "",
      currentPage: 1,
      limit: 10,
      form: {
        lvl: "",
        min: "",
        max: "",
        rebate: ""
      },
      rules: {
        lvl: [{ required: true, message: "必填项不可以为空", trigger: "blur" }],
        min: [{ required: true, message: "必填项不可以为空", trigger: "blur" }],
        max: [{ required: true, message: "必填项不可以为空", trigger: "blur" }],
        rebate: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      },
      tableData: []
    };
  },
  created() {
    this.initdata({ page: this.currentPage, limit: this.limit });
  },
  methods: {
    //添加
    add() {
      this.editForm("新增", true, {});
    },

    //表格编辑
    handleEdit(row) {
      this.editForm("编辑", true, DeepData(row));
    },

    //表格删除
    handleDelete(index, row) {
      this.$confirm("确认删除吗？", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(async () => {
          let { data } = await this.$http.allAgency.DeleteAllAgency({
            id: row.id
          });
          if (data.code === 1 && data.msg === "ok") {
            this.initdata({ page: this.currentPage, limit: this.limit });
          }
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },

    //页容量变化
    handleSizeChange(num) {
      this.limit = num;
      this.currentPage = 1;
      this.initdata({ page: this.currentPage, limit: this.limit });
    },

    //页码变化
    handleCurrentChange(pageNum) {
      this.currentPage = pageNum;
      this.initdata({ page: this.currentPage, limit: this.limit });
    },

    //提交
    onSubmit(formName, type) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          Object.keys(this.form).forEach(item => {
            this.form[item] = +(this.form[item]);
          });
          if(this.form.min > this.form.max){
             this.$message({
              type: "info",
              message: "流水区间下限不可以超过流水区间上限!!"
            });
            return false
          }
          if (this.form.rebate < 250) {
            if (type === "新增") {
              //发送post
              let { data } = await this.$http.allAgency.PostAllAgency(
                this.form
              );
              if (data.code === 1 && data.msg === "ok") {
                this.initdata({ page: this.currentPage, limit: this.limit });
                this.$message({
                  type: "success",
                  message: "添加成功!!"
                });
              }else{
                this.$message({
                  type: "warning",
                  message: "添加失败!!"
                });
              }
            } else if (type === "编辑") {
              //发送put
              let { data } = await this.$http.allAgency.PutAllAgency(this.form);
              if (data.code === 1 && data.msg === "ok") {
                this.initdata({ page: this.currentPage, limit: this.limit });
                this.$message({
                  type: "success",
                  message: "编辑成功!!"
                });
              }else{
                this.$message({
                  type: "warning",
                  message: "编辑失败!!"
                });
              }
            }
            this.editForm("新增", false, {});
          } else {
            this.$message({
              type: "info",
              message: "每万返佣额度输入最大值不超过250"
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    editForm(title, visible, form) {
      this.title = title;
      this.visible = visible;
      this.form = form;
    },

    async initdata(params) {
      let { data } = await this.$http.allAgency.GetAllAgency(params);
      this.tableData = DeepData(data.data);
      this.total = data.total;
    }
  }
};



